.burger-btn {
    width: 20px;
    height: 20px;
    margin-left: 20px;
    margin-right: 10px;
    position: relative;
    cursor: pointer;
}
.burger-btn span {
    position: absolute;
    top: 9px;
    width: 20px;
    background-color: silver;
    height: 2px;
}

.burger-btn:before {
    content: '';
    position: absolute;
    top: 0;
    width: 20px;
    background-color: silver;
    height: 2px;
}

.burger-btn:after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 20px;
    background-color: silver;
    height: 2px;
}

.nav-wrapper {
    display: flex;
    align-items: center;
}

.offset80 {
    margin-top: 80px;
}